.Home{
    margin-top: 30px;
    display:flex;
    width:100%;
    height:100vh;
    position:relative;
    overflow:hidden; 
    }



.background{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:url(../Img/Background.png) no-repeat center;
    background-size: cover;
    z-index:-1;
  }

.Fetch{
  text-align: center;
    margin-top: 35px;
    width:100%;
    height:200px;
    background: rgba(15, 15, 15, 0.873);
    backdrop-filter: blur(5px);
    padding: 25px 30px;
    border-radius: 5px;
    font-size: large;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: aliceblue;
    
  }
  .butonnnn{
    margin-top: 10px;
    margin-left: 5px;
    background-color: #f1f1f1;
    color: black;
    font-size: 16px;
    padding: 6px 6px;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    text-align: center;
  }

  .butonnnn:hover{
    background-color: black;
    color: white;
  }