.navi {
    display: flex;
    position: absolute;
    background-color: rgba(3, 17, 46, 0.885);
    width:100%;
    height: 30px;
    backdrop-filter: blur(5px);
}

.appname {
    padding: 5px;
    color: white;
    text-decoration: none;
    font-size: larger;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    vertical-align: middle;
}

.Abut{
    padding: 5px;
    color: white;
    text-decoration: none;
    font-size: larger;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    vertical-align: middle;
}
.Abut1{
    color: white;
    text-decoration: none;
}

.Icon{
    border-radius: 10px;
    padding: 5px 5px;
}


@media screen and (max-width : 500px) {

    .appname {
        padding: 5px;
        color: white;
        text-decoration: none;
        font-size: small;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        vertical-align: middle;
    }
    .Abut{
        padding: 5px;
        color: white;
        text-decoration: none;
        font-size: small;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        vertical-align: middle;
    }

}

