.videoTag{
    border-radius: 5px;
    height: 355px;
    width:100%;
    position:absolute;
    object-fit: cover;
  }



.Weather{
    margin-left: 5px;
    margin-top: 2px;
    width:100%;
    height:300px;
    background:rgba(15, 15, 15, 0.363);
    backdrop-filter: blur(0px);
    padding: 25px 30px;
    border-radius: 5px;
}
.Info{
      margin-top: 10px;
      margin-left: 32px;
      font-size: large;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      color: aliceblue;
}

.WeatherDescription{
    padding: 25px 30px;
    text-align: center;
    margin-left: 5px;
    width:100%;
    height:300px;
    border-radius: 5px;
    margin-top: 2px;
    background:rgba(15, 15, 15, 0.363);
    backdrop-filter: blur(0px);
}



.loc{
    width: 40px;
    height: 40px;
}
.Temp{
    width: 40px;
    height: 40px;
}
.Max{
    width: 40px;
    height: 40px;
}
.Min{
    width: 40px;
    height: 40px;
}



.SunData{
    margin-left: 5px;
    margin-top: 2px;
    margin-right: 5px;
    width:100%;
    height:300px;
    background:rgba(15, 15, 15, 0.363);
    backdrop-filter:blur(0px);
    padding:25px 30px;
    border-radius:5px;
}

.Rise{
    width: 40px;
    height: 40px;
}
.Set{
    width: 40px;
    height: 40px;
}
.desc{
    width:120;
    height: 120px;
}
.Wspeed{
    width: 25px;
    height: 25px;
    margin-left: 8px;
}
.Humi{
    width: 25px;
    height: 25px;
    margin-left: 8px;
}


@media screen and (max-width : 500px) {
    .videoTag{
        border-radius: 5px;
        height: 318px;
        width:100%;
        position:absolute;
        object-fit: cover;
      }
    .Weather{
        padding:3px 3px;
        width:100%;
        height:145px;
    }
    .Info{
        font-family: Arial;
        font-size: small;
        margin-left: 0px;
    }
    .WeatherDescription{
        text-align: center;
        width:110px;
        height:100px;
        margin-right: 5px;
    }
    .SunData{
        height: 155px;
        position:fixed;
        margin-top: 155px;
        padding: 3px 3px;
        margin-left: 5px;
        width:359px;
    }

    .loc{
        width: 15px;
        height: 15px;
    }
    .Temp{
        width: 15px;
        height: 15px;
    }
    .Max{
        width: 15px;
        height: 15px;
    }
    .Min{
        width: 15px;
        height: 15px;
    }
    .desc{
        width:50px;
        height: 50px;
    }

    .Rise{
        width: 15px;
        height: 15px;
    }
    .Set{
        width: 15px;
        height: 15px;
    }
    .Wspeed{
        width: 15px;
        height: 15px;
        margin-left: 2px;
    }
    .Humi{
        width: 15px;
        height: 15px;
        margin-left: 2px;
    }

}

